<template>
    <div>
        <!-- 提醒 -->
        <div class="header_title">
            <div class="tab_box">
                <small @click="tabclick(1)" class="remind" :class="activeclass==1?'activeclass':''">提醒</small>
                <small @click="tabclick(2)" class="overdue" :class="activeclass==2?'activeclass':''">逾期</small>
            </div>
            <div class="filter_box">
                <el-select v-model="empId" size="mini" filterable clearable placeholder="业务员">
                    <el-option
                    v-for="item in emplist"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                    >
                    </el-option>
                </el-select>
                <el-date-picker v-if="activeclass!=2" style="margin-left:10px;" size="mini" v-model="date" type="date" placeholder="选择日期">
                </el-date-picker>
            </div>
        </div>
        <div class="main_box">
            <div @click="editdata" class="item_box" v-for="(item,index) in datalist" :key="index">
                <div class="types_box" style="width:20%;">
                    <small>{{item.types}}</small>
                    <small v-if="item.fangshi!=''&&item.fangshi!=null">-</small>
                    <small>{{item.fangshi}}</small>
                </div>
                <div class="cus_name_box" style="width:50%;">
                    <small>{{item.cus_name}}</small>
                    <small>{{item.zuoyong}}</small>
                    <small>{{item.periods}}</small>
                </div>
                <div class="datetime" style="width:15%;">
                    {{item.date}}
                </div>
                <div class="btn_box" style="width:15%;">
                    <el-button @click.stop="newdata" type="text" size="mini">
                        去处理
                    </el-button>
                </div>
            </div>
        </div>
        <followplande ref="followplande"></followplande>
    </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from 'vue'
import followplande from "../followup_management/followplan_detail.vue";
export default {
    components: {
    followplande,
  },
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            activeclass:1,
            empId:'',
            emplist:[],
            date:'',
            datalist:[
                {
                    types:'跟进',
                    fangshi:'电话',
                    cus_name:'【上海普选医药科技有限公司】',
                    zuoyong:'【介绍公司产品】',
                    date:'2021-09-09 14:20',
                    periods:''
                },
                {
                    types:'证照到期',
                    fangshi:'',
                    cus_name:'【上海普选医药科技有限公司】',
                    zuoyong:'【xx证】',
                    date:'2021-09-09 14:20',
                    periods:'【第一期】'
                }
            ],
        })
        const tabclick = (n) => {
            proxy.activeclass = n
        }
        const editdata = () => {
            proxy.$refs.followplande.editshow();
        }
        const newdata = () => {
            proxy.$router.push({
                path:'/follow_record',
                query:{
                obj:{},
                isedit:0,
                isouter:1
                }
            })
            proxy.$store.state.isedit = 0

        }
        return {
            ...toRefs(state),
            tabclick,
            editdata,
            newdata
        }
    }
}
</script>

<style scoped>
.tab_box>small{
    font-weight: bold;
    font-size: 18px;
}
.activeclass{
    color: #0d84ff;
}
.header_title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
.overdue{
    margin-left: 20px;
}
.item_box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
</style>