<template>
    <div>
        <!-- 计划 -->
        <div class="header_title">
            <div class="tab_box">
                <small @click="tabclick(1)" class="remind" :class="activeclass==1?'activeclass':''">日任务</small>
                <small @click="tabclick(2)" class="overdue" :class="activeclass==2?'activeclass':''">周计划</small>
                <small @click="tabclick(3)" class="overdue" :class="activeclass==3?'activeclass':''">月计划</small>
                <small @click="tabclick(4)" class="overdue" :class="activeclass==4?'activeclass':''">季计划</small>
            </div>
            <div class="filter_box">
                <el-date-picker size="mini" v-model="date" :type="date_type" placeholder="选择日期">
                </el-date-picker>
            </div>
        </div>
        <div class="main_box">
            <div class="item_box" v-for="(item,index) in datalist" :key="index">
                <div class="types_box">
                    <small>{{item.types}}</small>
                    <small v-if="item.fangshi!=''&&item.fangshi!=null">-</small>
                    <small>{{item.fangshi}}</small>
                </div>
                <div class="cus_name_box">
                    <small>{{item.cus_name}}</small>
                    <small>{{item.zuoyong}}</small>
                </div>
                <div class="cus_name_box">
                    <small>{{item.yes}}</small>
                    <small>{{item.yesnum}}</small>
                </div>
                <div class="datetime">
                    <el-progress class="elprogress" width="35" type="circle" :percentage="item.plan_rate"></el-progress>
                </div>
                <div class="btn_box">
                    <el-button @click="newsuccess" type="text" size="mini">
                        去完成
                    </el-button>
                </div>
            </div>
        </div>
        <followplande ref="followplande"></followplande>
    </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from 'vue'
import followplande from "../followup_management/followplan_detail.vue"
export default {
    components: {
    followplande,
  },
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            activeclass:1,
            date:'',
            date_type:'date',
            datalist:[
                {
                    types:'跟进',
                    fangshi:'电话',
                    cus_name:'计划',
                    zuoyong:'10',
                    yes:'完成',
                    yesnum:'5',
                    date:'2021-09-09 14:20',
                    periods:'',
                    plan_rate:25
                },
                {
                    types:'证照到期',
                    fangshi:'',
                    cus_name:'计划',
                    zuoyong:'￥3,000.00',
                    yes:'完成',
                    yesnum:'￥0.00',
                    date:'2021-09-09 14:20',
                    periods:'【第一期】',
                    plan_rate:50
                }
            ],
        })
        const tabclick = (n) => {
            proxy.activeclass = n
            if(n==1){
                proxy.date_type = 'date'
            }else if(n==2){
                proxy.date_type = 'week'
            }else if(n==3){
                proxy.date_type = 'month'
            }else if(n==4){
                proxy.date_type = 'year'
            }
        }
        const newsuccess = () => {
            proxy.$refs.followplande.newshow();
        }
        return {
            ...toRefs(state),
            tabclick,
            newsuccess
        }
    }
}
</script>

<style scoped>
.tab_box>small{
    font-weight: bold;
    font-size: 18px;
}
.activeclass{
    color: #0d84ff;
}
.header_title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
.overdue{
    margin-left: 20px;
}
.item_box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
.item_box>div{
    width: 20%;
}
</style>
<style>
    .elprogress .el-progress__text{
      left: -8px;
    }
</style>