<template>
    <div>
        <!-- 审批中心 -->
        <div class="new_header">
            审批中心
        </div>
        <div class="cus_list_box">
            <div class="tab_plan_box">
                <div>
                    <el-badge :class="active==1?'active':''" :value="1" class="item">
                        <el-button @click="planclick(1)" size="small">月计划</el-button>
                    </el-badge>
                </div>
                <div>
                    <el-badge :class="active==2?'active':''" :value="2" class="item">
                        <el-button @click="planclick(2)" size="small">季计划</el-button>
                    </el-badge>
                </div>
            </div>
            <div class="table_box">
                <div @click="editdata" class="table_row_box" v-for="(item,index) in datalist" :key="index">
                    <div class="name_box">
                        {{item.name}}
                    </div>
                    <div class="sign_amount">
                        <div class="date_box">【{{item.date}}】</div>
                        <div class="sign_item" v-for="(imt,ind) in item.signlist" :key="ind">
                            {{ind+1}},<small>{{imt.types}}</small><small v-if="imt.caozuo!=''&&imt.caozuo!=null">-</small>
                            <small>{{imt.caozuo}}</small>
                            <small> {{imt.num}}</small>
                            <small> ￥{{imt.money}}</small>
                        </div>
                    </div>
                    <div class="date_box">
                        {{item.datetime}}
                    </div>
                    <div class="btn_box">
                        <el-button @click.stop="examine" type="text">
                            审批
                        </el-button>
                    </div>
                </div>
            </div>
        </div>
        <examine ref="examine"></examine>
    </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from 'vue'
import examine from '../plan_management/examinealert.vue'
export default {
    components:{examine},
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            datalist:[
                {
                    name:'王立',
                    date:'2021年9月',
                    signlist:[
                        {
                            types:'签约',
                            caozuo:'数量',
                            num:10,
                            money:'50,000.00'
                        },
                        {
                            types:'新增意向客户',
                            caozuo:'',
                            num:15,
                            money:'50,000.00'
                        }
                    ],
                    datetime:'2021-08-20 16:30'
                },
                {
                    name:'张三',
                    date:'2021年9月-11月',
                    signlist:[
                        {
                            types:'签约',
                            caozuo:'数量',
                            num:10,
                            money:'50,000.00'
                        },
                        {
                            types:'新增意向客户',
                            caozuo:'',
                            num:15,
                            money:'50,000.00'
                        }
                    ],
                    datetime:'2021-08-20 16:30'
                }
            ],
            active:1
        })
        const editdata = () => {
            if(proxy.active==1){
                proxy.$router.push("/planInformation");
            }else{
                proxy.$router.push("/planInformation");
            }
        }
        const examine = () => {
            proxy.$refs.examine.changestatus()
        }
        const planclick = (n) => {
            proxy.active = n
        }
        return {
            ...toRefs(state),
            editdata,
            examine,
            planclick
        }
    }
}
</script>

<style scoped>
.new_header{
    font-weight: bold;
    font-size: 18px;
    padding: 10px;
    box-sizing: border-box;
    text-align: left;
    border-bottom: 1px solid #eee;
}
.tab_plan_box{
    display: flex;
    align-items: center;
    padding-top: 15px;
    box-sizing: border-box;
}
.tab_plan_box>div{
    margin-left: 20px;
}
.table_row_box{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 10px 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
.active .el-button{
    color: #0d84ff;
    border-color: #0d84ff;
}
</style>