<template>
  <div>
    <!-- 销售环比 -->
    <div class="new_header">
      销售环比
      <div class="option_box">
        <select-tree
          :treeData="branchlist"
          @callBackFunc="gettreevalue"
        ></select-tree>
        <el-button @click="more('followStatistics')" class="elbutton" type="text" size="mini">
          更多>>
        </el-button>
      </div>
    </div>
    <div class="echarts_box">
      <div id="myEcharts" :style="{ width: '700px', height: '400px' }"></div>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, toRefs } from "vue";
import SelectTree from "../../components/selectTree.vue";
import * as echarts from "echarts";
export default {
  components: { SelectTree },
  setup() {
      const { proxy } = getCurrentInstance()
    const state = reactive({
      branchId: "",
      branchlist: [
        {
          id: 1,
          label: "乐成公司",
          children: [
            {
              id: 4,
              label: "质控品部",
              children: [
                {
                  id: 9,
                  label: "ZK营销部",
                },
              ],
            },
            {
              id: 4,
              label: "第三方物流部",
              children: [
                {
                  id: 9,
                  label: "WL营销部",
                },
              ],
            },
          ],
        },
      ],
    });
    const echartsfunc = () => {
      let chart = echart.init(document.getElementById("myEcharts"));
      // 把配置和数据放这里
      chart.setOption({
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ["product", "8月", "9月"],
            ["李四", 2000, 1000],
            ["王立", 3000, 5000],
            ["张三", 2200, 4500]
          ],
        },
        xAxis: { type: "category" },
        yAxis: {},
        series: [{ type: "bar" }, { type: "bar" }],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    };
    let echart = echarts;
    const more = (str) => {
      proxy.$router.push({
                path: "/reportform",
                query: {
                    obj: {},
                    str: str,
                    isouter:1
                },
            });
    }
    onMounted(()=>{
        proxy.echartsfunc()
    })
    return {
      ...toRefs(state),
      echartsfunc,
      more
    };
  },
};
</script>

<style scoped>
.new_header {
  display: flex;
  font-weight: bold;
  font-size: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  border-bottom: 1px solid #eee;
}
.elbutton {
  margin-left: 10px;
}
.option_box {
  display: flex;
  align-items: center;
}
</style>