                    {{item.amount}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from 'vue'
import SelectTree from "../../components/selectTree.vue";
export default {
    components: { SelectTree },
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            activeclass:1,
            empId:'',
            emplist:[],
            date:'',
            datalist:[
                {
                    name:'张三',
                    amount:'53,237.00'
                },
                {
                    name:'李四',
                    amount:'53,237.00'
                }
            ],
            branchId:'',
            branchlist: [
                {
                    id: 1,
                    label: "乐成公司",
                    children: [
                        {
                            id: 4,
                            label: "质控品部",
                            children: [
                                {
                                    id: 9,
                                    label: "ZK营销部",
                                }
                            ],
                        },
                        {
                            id: 4,
                            label: "第三方物流部",
                            children: [
                                {
                                    id: 9,
                                    label: "WL营销部",
                                }
                            ],
                        },
                    ],
                },
            ],
        })
        const tabclick = (n) => {
            proxy.activeclass = n
        }
        const more = (str) => {
      proxy.$router.push({
                path: "/reportform",
                query: {
                    obj: {},
                    str: str,
                    isouter:1
                },
            });
    }
        return {
            ...toRefs(state),
            tabclick,
            more
        }
    }
}
</script>

