<template>
    <div>
        <!-- 新增跟进 -->
        <div class="new_header">
            跟进
            <div class="option_box">
                <select-tree
                :treeData="branchlist"
                @callBackFunc="gettreevalue"
                ></select-tree>
                <el-button @click="newdata" class="elbutton" type="primary" size="mini">
                    新增跟进
                </el-button>
            </div>
        </div>
        <div class="cus_list_box">
            <div class="cus_item_box">
                <div class="new_cus_box">
                    <div class="title_box" style="margin-bottom:20px;">今日跟进</div>
                    <small @click="newday('followStatistics')">8</small>
                </div>
                <div class="btn_box">
                    <div><el-button size="mini" @click="newday('followStatistics')">
                        同比
                    </el-button></div>
                    <div style="margin-top:10px;"><el-button size="mini" @click="newday('followStatistics')">
                        环比
                    </el-button></div>
                </div>
                <div class="my_new_cus_box">
                    <div class="title_box" style="margin-bottom:20px;">我的今日跟进</div>
                    <small @click="mynewdata">4</small>
                </div>
            </div>
            <div class="cus_item_box">
                <div class="new_cus_box">
                    <div class="title_box" style="margin-bottom:20px;">本月跟进</div>
                    <small @click="newday('followStatistics')">36</small>
                </div>
                <div class="btn_box">
                    <div><el-button size="mini" @click="newday('followStatistics')">
                        同比
                    </el-button></div>
                    <div style="margin-top:10px;"><el-button size="mini" @click="newday('followStatistics')">
                        环比
                    </el-button></div>
                </div>
                <div class="my_new_cus_box">
                    <div class="title_box" style="margin-bottom:20px;">我的本月跟进</div>
                    <small @click="mynewdata">16</small>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from 'vue'
import SelectTree from "../../components/selectTree.vue";
export default {
    components: { SelectTree },
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            branchId:'',
            branchlist: [
                {
                    id: 1,
                    label: "乐成公司",
                    children: [
                        {
                            id: 4,
                            label: "质控品部",
                            children: [
                                {
                                    id: 9,
                                    label: "ZK营销部",
                                }
                            ],
                        },
                        {
                            id: 4,
                            label: "第三方物流部",
                            children: [
                                {
                                    id: 9,
                                    label: "WL营销部",
                                }
                            ],
                        },
                    ],
                },
            ],
        })
        const gettreevalue = (val) => {
            console.log(val)
        }
        const newdata = () => {
            proxy.$router.push({
                path:'/follow_record',
                query:{
                obj:{},
                isedit:0,
                isouter:1
                }
            })
            proxy.$store.state.isedit = 0
        }
        const mynewdata = () => {
            proxy.$router.push({
                path:'/followup',
                query:{
                    obj:{},
                    str:'second',
                    isouter:1
                }
            })
        }
        const newday = (str) => {
            proxy.$router.push({
                path: "/reportform",
                query: {
                    obj: {},
                    str: str,
                    isouter:1
                },
            });
        }
        return {
            ...toRefs(state),
            gettreevalue,
            newdata,
            mynewdata,
            newday
        }
    }
}
</script>

<style scoped>
.new_header{
    display: flex;
    font-weight: bold;
    font-size: 18px;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
}
.elbutton{
    margin-left: 10px;
}
.cus_item_box{
    display: flex;
    align-items:flex-start;
    justify-content: space-between;
    margin-top: 30px;
}
.cus_item_box>div>small{
    font-size: 20px;
    color: #0d84ff;
}
.cus_list_box{
    padding: 30px 60px;
    box-sizing: border-box;
    padding-top: 0;
}
.option_box{
    display: flex;
    align-items: center;
}
</style>